<div class="hello"><h1><router-link v-if="book &amp;&amp; uid &amp;&amp; book.length == 0" to="/friends">您的名簿目前沒有人，按此找朋友</router-link></h1><loader v-show="!users"></loader><div class="ui divider"></div><div class="ui four doubling cards container" v-if="toList(places).length &gt; 0 &amp;&amp; toList(users).length &gt; 0"><div class="ui card" v-for="(h, index) in searchBy(places, mySearch)" :key="index" v-show="book &amp;&amp; book.indexOf(h.uid) &gt; -1"><card :h="h" :full="false" :book="book" :mySearch="mySearch" @locate="locate" @addBook="addBook" @removeBook="removeBook"></card></div><div class="ui card" v-for="(h, index) in searchBy(users, mySearch)" :key="index" v-show="book &amp;&amp; h &amp;&amp; book.indexOf(h.uid) &gt; -1"><card :h="h" :full="false" :book="book" :mySearch="mySearch" @locate="locate" @addBook="addBook" @removeBook="removeBook"></card></div></div><br><br><br><br><br></div>